.table {
  width: 100%;
}
.btn-danger {
  margin-top: 5px;
}

td{
  text-align:  center;
  vertical-align: middle;
}

@media only screen and (max-width: 600px) {
  .td-custom{
    max-width: 0;
    text-overflow: ellipsis;
    overflow: hidden;
    min-width: 110px;
    height: 1.2em;
    white-space: nowrap;
  }
}
